<template>
  <div id="app">
    <nav-bar />
    <div v-if="this.$store.state.posts.length > 0 && this.$store.state.pages.length > 0">
      <router-view id="displayed-page" />
      <footer-area></footer-area>
    </div>
    <div v-else>
      <!-- Loading animation -->
      <div class="container">
        <div class="row justify-content-center" style="padding-top: 100px;">
          <pulse-loader color="#003b7d"></pulse-loader>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript" src="//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5d20c9b245909795"></script>
<script>
import NavBar from "@/components/NavBar.vue";
import FooterArea from "@/components/FooterArea.vue";
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  components: {
    NavBar,
    FooterArea,
    PulseLoader
  },
  async beforeMount() {
    console.log(process.env.VUE_APP_MODE)
    await this.$store.dispatch("getSettings")
    await this.$store.dispatch("getPosts")
    await this.$store.dispatch("getPages")
  },
};
</script>

