<template>
    <button v-if="buttonValue"
        :style="{ 'background-color': this.$store.state.secondary_colour}"
        class="flat-butt" @click="routeTo('trainingcourses')">
        <font-awesome-icon :icon="buttonValue.acf.icon" class="btn-icon"></font-awesome-icon>
        <span style="padding-left: 10px;">{{buttonValue.acf.text}}</span>
     </button>
</template>

<script>
import RouteTo from "@/mixins/routeto.js"

export default {
    mixins: [RouteTo],
    props: {
        nameValue: String,
    },
    computed: {
        buttonValue() {
            return this.$store.state.posts.find(x => x.post_title == this.nameValue)
        }
  }
}
</script>

