<template>
  <div class="container">
    <div class="row" id="quote">
      <div class="col-12">
        <transition name="fade" mode="out-in">
          <div :key="quote">
            <h1 class="text-center" v-html="quote"></h1>
          </div>
        </transition>
        <transition name="fade" mode="out-in">
          <div :key="author">
            <h3 class="text-center">{{author}}</h3>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      quotes: [],
      quote: "",
      author: "",
      index: 0
    };
  },
  mounted() {
    this.$store.state.posts.forEach(x => {
          x.categories.forEach(y => {
          if(y.cat_name == 'Quote')  this.quotes.push(x)
        })
      });
    this.quote = this.quotes[0].post_content
    this.author = this.quotes[0].post_title
    this.$nextTick(function() {
      window.setInterval(() => {
        this.countDown()
      }, 30000)
    });
  },
  methods: {
    countDown() {
      if (this.index >= this.quotes.length) {
        this.index = 0
      } 
      this.quote = this.quotes[this.index].post_content
      this.author = this.quotes[this.index].post_title
      this.index++
    }
  },
};
</script>

<style>
#quote {
  padding-top: 100px;
  padding-bottom: 100px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
