import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
const api_root = process.env.VUE_APP_ROOT_API
const mode = process.env.VUE_APP_MODE

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pages: [],
    posts: [],
    primary_colour: '',
    secondary_colour: '',
    tertiary_colour: '',
    quarternary_colour: ''
  },
  getters: {
    sortedPosts: state=> {
      return state.posts.sort((a, b) => a.acf.order - b.acf.order)
    } 
  },
  mutations: {
    addSettings (state, value) {
      let settings = value.find(x => x.template == 'settingspage.php')
      state.primary_colour = settings.acf.primary_colour
      state.secondary_colour = settings.acf.secondary_colour
      state.tertiary_colour = settings.acf.tertiary_colour
      state.quarternary_colour = settings.acf.quarternary_colour
    },
    addPages (state, value) {
      state.pages = value;
    },
    addPosts (state, value) {
      state.posts = value;
    },
  },
  actions: {
    async getSettings ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/settings')
        .then(response => {
          data = response.data
          commit('addSettings', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getPages ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/pages')
        .then(response => {
          data = response.data
          commit('addPages', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getPosts ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/posts')
        .then(response => {
          data = response.data
          commit('addPosts', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
  }
})
