  components: { Quote, ContactForm },
  computed: {
    ContactInfo() {
      let contact_info = this.$store.state.posts.find(x => x.post_title == 'Contact Info')
      let splitAddress = contact_info.acf.address.split('\r')
      let joinedAddress = '<p>' + splitAddress.join(' <p/> <p>') + '<g(p/>'
      contact_info.acf.address = joinedAddress
      return contact_info
    }
  }
};
</script>

