<template>
<div>
  <div>
    <form>
      <div class="form-row justify-content-center">
        <div class="col-md">
          <div class="col form-item">
            <input
              type="text"
              v-model="name"
              class="form-control form-input"
              placeholder="Name"
            />
          </div>
          <div class="col form-item">
            <input
              type="text"
              v-model="company"
              class="form-control form-input"
              placeholder="Company"
            />
          </div>
          <div class="col form-item">
            <input
              type="text"
              v-model="jobTitle"
              class="form-control form-input"
              placeholder="Job Title"
            />
          </div>
          <div class="col form-item">
            <input
              type="text"
              v-model="email"
              class="form-control form-input"
              placeholder="Email"
            />
          </div>
          <div class="col form-item">
            <input
              type="text"
              @keypress="isNumber($event)"
              v-model="number"
              class="form-control form-input"
              placeholder="Phone"
            />
          </div>
        </div>
        <div class="col-md">
          <div class="col form-item" v-if="enquiryPresent">
            <textarea class="form-control form-input" v-model="enquiryArea" placeholder="Enquiry" />
          </div>
          <div class="col form-item">
            <button
              type="button"
              class="flat-butt"
              :style="{ 'background-color': this.$store.state.quarternary_colour}"
              @click="postValue()"
            >{{buttonText}}</button>
          </div>
          <div v-if="errors">
            <b-alert v-for="(error, index) in errors"
            :key="index"
            variant="danger"
            :show="errors != []">{{error}}</b-alert>
          </div>
        </div>
      </div>
    </form>
  </div>
</div>
</template>

<script>
import NavigateTo from "@/mixins/navigateto.js"
import axios from 'axios'

export default {
    mixins: [NavigateTo],
    props: {
      formType: String,
      location: String,
      enquiryPresent: Boolean,
      buttonText: String,
      isDownload: Boolean
    },
    data() {
      return {
        name: "",
        company: "",
        jobTitle: "",
        email: "",
        number: "",
        enquiryArea: "",
        errors: [],
        reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
      };
    },
    methods: {
      postValue(){
        this.errors=[];
        
        let validationError = this.validateFormValues();

        if(validationError != "")
        {
          this.errors.push(validationError)
          return;
        } 

        let enquiryMessage = this.enquiryPresent ? `\r\nEnquiry: ${this.enquiryArea}`: ""
        let locationValue = this.location != "" ? this.location :  `https://tolerance-analysis.co.uk/#${this.$route.path}`

        let content =  `Enquiry Type: ${this.formType} form submission\r\nName :  ${this.name}\r\nEmail :  ${this.email}\r\nCompany :  ${this.company}\r\nJob Title: ${this.jobTitle}\r\nPhone Number: ${this.number}${enquiryMessage}\r\nLocation:/${locationValue}`;
        let payload = {
          "post": process.env.VUE_APP_COMMENT_ID,
          "author_email": this.email,
          "content":content,
          "author_name": this.name
        }
        axios
          .post(process.env.VUE_APP_ROOT_API + 'wp/v2/comments', payload)
          .then((response) => {
            this.resetValues()
          },)
          .catch(e => {
            console.log(e);
            this.errors.push("Sorry, form not submitted. Possible double submission. Please try again")
          })
    },
    validateFormValues() {
      if (this.name == "") return "Please enter name value";
      if (this.email == "") return "Please enter an email";
      if (!this.isEmailValid(this.email)) return "Please enter a valid email format";
      if (this.number == "") return "Please enter a phone number";

      return"";
    },
    isEmailValid: function(email) {
      return (email == "")? "" : (this.reg.test(this.email)) ? true: false;
    },
    resetValues() {
      this.name = "";
      this.company = "";
      this.jobTitle = "";
      this.email = "";
      this.number = "";
      this.enquiryArea = "";
      
      if(this.isDownload) {
        this.navigateTo(this.location)
      }

    },
    isNumber: function(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46 && charCode != 43) {
        evt.preventDefault();
      } else {
        return true;
      }
    }
  }
};
</script>

<style scoped>
.form-item {
  padding-bottom: 10px;
}
</style>
