import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/softwaresolutions',
      name: 'softwaresolutions',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/SoftwareSolutions.vue')
    },
    {
      path: '/trainingcourses',
      name: 'trainingcourses',
      component: () => import(/* webpackChunkName: "about" */ './views/TrainingCourses.vue')
    },
    {
      path: '/Resources',
      name: 'recources',
      component: () => import(/* webpackChunkName: "about" */ './views/Resources.vue')
    },
    {
      path: '/Consultancy',
      name: 'Consultancy',
      component: () => import(/* webpackChunkName: "about" */ './views/Consultancy.vue')
    },
    {
    path: '/Contact',
    name: 'contact',
    component: () => import(/* webpackChunkName: "about" */ './views/Contact.vue')
  }
  ]
})
