<template>
  <div v-if="this.$store.state.posts">
    <div class="form-row" id="box"
    :style="{ 'border-color': this.$store.state.primary_colour}">
      <div class="container">
        <h4 v-html="ContactInfo.post_content"></h4>
        <hr />
        <div class="row content" style="margin: 10px">
          <div class="col-s marg">
            <font-awesome-icon :icon="ContactInfo.acf.phone_icon"></font-awesome-icon>
          </div>
          <div class="col-s">{{ContactInfo.acf.phone_number}}</div>
        </div>
        <div class="row content" style="margin: 10px">
          <div class="col-s marg">
            <font-awesome-icon :icon="ContactInfo.acf.email_icon"></font-awesome-icon>
          </div>
          <div class="col-s content">{{ContactInfo.acf.email_address}}</div>
        </div>
        <div class="row content" style="margin: 10px">
          <div class="col-s marg">
            <font-awesome-icon :icon="ContactInfo.acf.address_icon"></font-awesome-icon>
          </div>
          <div class="col-s" v-html="ContactInfo.acf.address">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    ContactInfo() {
      let contact_info = this.$store.state.posts.find(x => x.post_title == 'Contact Info')
      let addressWithLineBreaks = contact_info.acf.address.split('\r').join(' <br/>')
      contact_info.acf.address = addressWithLineBreaks
      return contact_info
    },
  }
};
</script>

<style scoped>
#box {
  border-width: 0.5px;
  border-style: solid;
  padding: 10px;
}
.logo {
  height: 90%;
  width: 90%;
}
.marg {
  margin-right: 5px;
}
 @media only screen and (max-width: 992px) {
  .content {
      justify-content: center;
  }
}
</style>