<template>
  <div v-if="homePage">

    <three-bar-banner
    style="padding-bottom: 3%"/>

    <page-content
    :contentProp="homePage.post_content"
    :sidebarProp="homePage.acf.sidebar">
    </page-content>

  </div>
</template>

<script>
import RouteTo from "@/mixins/routeto.js"
import ThreeBarBanner from "@/components/ThreeBarBanner.vue";
import PageContent from "@/views/PageContent/PageContent.vue"

export default {
  name: "home",
  mixins: [RouteTo],
  components: {
    ThreeBarBanner,
    PageContent
  },
  computed: {
    homePage() {
      return this.$store.state.pages.find(x => x.post_title == 'Home')
    },
  }
};
</script>

