<template>
    <div v-if="value"
        class="form-row title-text-alternative text-center">
        <a :style="{ 'background-color': this.$store.state.tertiary_colour}"
            class="flat-butt" 
            v-scroll-to="'#contact-area'">
            <font-awesome-icon :icon="value.acf.icon"></font-awesome-icon>
            <span style="padding-left: 10px;">{{value.acf.text}}</span>
        </a>
    </div>
</template>

<script>
export default {
    props: {
        nameValue: String,
    },
    computed: {
        value() {
            return this.$store.state.posts.find(x => x.post_title == this.nameValue)
        }
    }
}
</script>

